import React from 'react';
import { Helmet } from 'react-helmet';

import { useParams } from 'react-router-dom';

import { Loading, Product, ProductDescription, ProductImage } from './style';

const SingleProduct = () => {
  const [product, setProduct] = React.useState(null);
  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState(null);
  const { id } = useParams();

  React.useEffect(() => {
    async function fetchProduct(url) {
      try {
        setLoading(true);
        const response = await fetch(url);
        const json = await response.json();
        setProduct(json);
      } catch (erro) {
        setError({ message: 'Não foi possível completar a sua solicitação' });
      } finally {
        setLoading(false);
      }
    }
    fetchProduct(`https://ranekapi.origamid.dev/json/api/produto/${id}`);
  }, [id]);

  if (loading) return <Loading className="loading"></Loading>;
  if (error) return <p>{error}</p>;
  if (product === null) return null;
  return (
    <>
      <Product className="animeLeft">
        <Helmet>
          <title>
            {product.nome} - R$ {product.preco}
          </title>
        </Helmet>
        <ProductImage>
          {product.fotos.map((photo) => (
            <img key={photo.src} src={photo.src} alt={photo.titulo} />
          ))}
        </ProductImage>
        <ProductDescription>
          <h1>{product.nome}</h1>
          <span>R$ {product.preco}</span>
          <p>{product.descricao}</p>
        </ProductDescription>
      </Product>
    </>
  );
};

export default SingleProduct;
